<template>
  <div class="login">
    <div class="text">
    </div>
    <transition-group name="el-fade-in">
      <div v-show="!show" class="transition-box" key="login">
        <Loginmodel />
      </div>
      <div v-show="show" class="transition-box" key="register">
        <Registermodel />
      </div>
    </transition-group>
  </div>
</template>

<script>
import Loginmodel from "../components/LoginModel/loginmodel";
import Registermodel from "../components/LoginModel/registermodel";
export default {
  data() {
    return {
      show: false
    };
  },
  components: { Loginmodel, Registermodel },
  mounted() {
    this.$nextTick(() => {
      $(".login").ripples({
        resolution: 500,
        dropRadius: 5, //px
        perturbance: 0.04
      });
    });
  },
  beforeRouteLeave(to, from, next) {
    this.$nextTick(() => {
      this.destory();
    });
    next();
  },
  methods: {
    toggle() {
      this.show = !this.show;
    },
    destory() {
      $(".login").ripples("destroy");
    }
  }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/index.scss";
.login {
  width: 100%;
  height: 100%;
  font-size: $font_large;
  background {
    image: url("/api/imgs/webbg6.jpg");
    size: 100% 100%;
  }
  background-image: url("/api/imgs/webbg6.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  .text {
    @include font1_20;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
