import Vue from 'vue'
import Router from 'vue-router'
import Sheets from '@/views/Sheets.vue'
import Home from './views/Home.vue'
import Login from './views/Login.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'sheet',
      component: Sheets
    },
    {
      path: '/main',
      // name: 'main',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/Main.vue'),
      children: [
        {
          path: '/',
          redirect: '/blog'
        },
        {
          path: '/home',
          name: 'home',
          component: Home
        },
        {
          path: '/helloworld',
          name: 'helloworld',
          component: () => import('@/views/HelloWorld.vue'),
        },

        {
          path: '/chatroom',
          name: 'chatroom',
          component: () => import('@/views/chatroom.vue')
        },
        {
          path: '/blog',
          component: () => import('@/views/blog.vue'),
          children: [
            {
              path: '/',
              redirect: '/bloglist',
            },
            {
              path: '/bloglist',
              name: 'bloglist',
              component: () => import('@/views/bloglist.vue')
            },
            {
              path: '/blogreading/:blogId',
              name: 'blogreading',
              component: () => import('@/views/blogreading.vue')
            },
          ]
        },
        {
          path: '/about',
          name: 'about',
          component: () => import('@/views/About.vue')
        },
        {
          path: '/chain',
          name: 'chain',
          component: () => import('@/components/chain.vue')
        }
      ]
    },
    {
      path: '/helloreading/:bookId',
      name: 'helloreading',
      component: () => import('@/components/Hellomodules/helloreading.vue')
    },
    {
      path: '/hellowritebook/:bookId',
      name: 'hellowritebook',
      component: () => import('@/components/Hellomodules/hellowritebook.vue')
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    }
  ]
})
