import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

//css
import '@/assets/scss/config.scss'
import '@/assets/scss/index.scss'
import '@/assets/css/reset.css'
import '@/assets/css/iconfont.css'
import 'element-ui/lib/theme-chalk/index.css'
import 'mavon-editor/dist/css/index.css'
import "@/assets/js/jquery.min.js";
import "@/assets/js/jquery.ripples.js";

//
import ElementUI from 'element-ui'
Vue.use(ElementUI);
import VueParticles from 'vue-particles'
Vue.use(VueParticles)
import VueLazyLoad from 'vue-lazyload'
Vue.use(VueLazyLoad, {
  loading: '/api/imgs/webbg3.jpg',
})
import fastclick from 'fastclick';
fastclick.attach(document.body);
import mavonEditor from 'mavon-editor'
Vue.use(mavonEditor)
import vueClapButton from 'vue-clap-button'
Vue.use(vueClapButton);
import moment from 'moment'

import 'fullpage.js/vendors/scrolloverflow';
import VueFullPage from 'vue-fullpage.js'
Vue.use(VueFullPage)

Vue.prototype.$moment = moment;
Vue.prototype.$Buse = new Vue()
Vue.config.productionTip = false

Vue.filter('formatTimeAgo', function (value) {
  return moment(value).locale('zh-cn').fromNow();
})


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
