<template>
  <div class="registermodel">
    <div class="transition-box">
      <div class="regModel">
        <transition name="fade" mode="in-out">
          <div class="avatar">
            <div></div>
            <div class="show">
              <input
                class="selectfile"
                type="file"
                ref="uploadAva"
                name="avatar"
                @change="uploadAvatar"
                alt=""
                required
              />
              <div class="showfile">
                <img src="../../assets/imgs/userBorder1.png" width="60" height="60" alt />
                <img :src="avatar" width="60" height="60" style="border-radius:50%;" alt />
              </div>
            </div>
          </div>
        </transition>
        <div class="username">
          <div></div>
          <div class="tapUser">
            <input type="text" name="username" v-model="username" autocomplete="off" />
          </div>
        </div>
        <div class="password">
          <div></div>
          <div class="tapPsd">
            <input type="password" name="password" v-model="password" />
          </div>
        </div>
        <div class="regControl control">
          <button class="registerBtn btn" @click="register"></button>
          <span class="tipText" @click="toggle"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { uploadAvatar, Register } from "@/api/index";
export default {
  data() {
    return {
      username: "",
      password: "",
      avatar: "http://localhost/imgs/headImg.png",
      avatarMimes: [
        "image/jpeg",
        "image/pjpeg",
        "image/jpeg",
        "image/pjpeg",
        "image/jpeg",
        "image/pjpeg",
        "image/png",
        "image/x-png",
        "image/gif"
      ]
    };
  },
  methods: {
    uploadAvatar() {
      let avatar = this.$refs["uploadAva"].files[0];
      if (!this.avatarMimes.includes(avatar.type)) {
        return this.$notify({
          title: "",
          message: "",
          iconClass: "el-icon-warning-outline",
          duration: 2500
        });
      }
      let params = new FormData();
      params.append("avatar", avatar);
      uploadAvatar(params).then(res => {
        if (res.data.code === 200) {
          this.avatar = res.data.data.avatar;
        } else {
          this.$notify({
            title: "",
            message: res.data.msg,
            iconClass: "el-icon-warning-outline",
            duration: 2500
          });
        }
      });
    },
    register() {
      if (this.username.length != 0 && this.password.length != 0) {
        let params = {
          username: this.username,
          password: this.password,
          avatar: this.avatar
        };
        Register(params).then(res => {
          if (res.data.code === 200) {
            this.$notify({
              title: "",
              message: "",
              iconClass: "el-icon-check",
              duration: 2500
            });
            this.toggle();
          } else {
            this.$notify({
              title: "",
              message: res.data.msg,
              iconClass: "el-icon-warning-outline",
              duration: 2500
            });
          }
        });
      } else {
        this.$notify({
          title: "",
          message: "",
          iconClass: "el-icon-warning-outline",
          duration: 2500
        });
      }
    },
    toggle() {
      this.$parent.$parent.toggle();
      this.reset();
    },
    reset() {
      this.username = "";
      this.password = "";
      this.avatar = "http://localhost/imgs/headImg.png";
    }
  }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/index.scss";

.registermodel {
  @include divabcenter;
  @include modelbase;
  @include font1_24;
  @include background($background-color-theme, 0.6);
  & .avatar .show {
    position: relative;
    & .selectfile {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
      z-index: 102;
      opacity: 0;
    }
    & .showfile {
      position: relative;
      width: 60px;
      height: 60px;
      margin: 0 auto;
      & img {
        position: absolute;
        left: 50%;
        top: 0;
        &:first-child {
          z-index: 100;
          width: 80px;
          height: 80px;
          transform: translateX(-50%) translateY(-7px);
        }
        &:last-child {
          transform: translateX(-50%);
          opacity: 1;
          transition: all 0.5s;
        }
      }
    }
  }
  & .username .tapUser,
  & .password .tapPsd {
    width: 240px;
    margin: 10px auto;
  }
  & .control {
    position: relative;
    height: 30px;
    width: 100%;
    margin-top: 30px;
    & .btn {
      @include font1_20;
      @include background($background-color-theme);
      @include divabcenter-X(100px,30px);
      border: 5px solid transparent;
      box-sizing: border-box;
      outline: none;
      border-image: url("../../assets/imgs/border0.png") 50 80 50 80 stretch;
    }
    & .tipText {
      font-size: 16px;
      cursor: pointer;
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
}
input {
  width: 100%;
  height: 30px;
  @include font1_20;
  border: 5px solid transparent;
  padding: 0 5px;
  border-image: url("../../assets/imgs/border0.png") 50 80 50 80 stretch;
  @include background($background-color-theme);
  outline: none;
}
</style>

