export let baseUrl = process.env.NODE_ENV === 'development' ? '/api' : 'http://www.qctx.work';
import Axios from 'axios';

//

//
export const uploadAvatar = (params) => {
	return Axios({
		url: baseUrl + '/avatar',
		method: 'POST',
		data: params
	})
}
