<template>
  <div class="loginmodel">
    <div class="transition-box">
      <div class="loginModel">
        <div class="avatar" :style="{opacity:(dafaultavatar.length=== 0?'0':'1')}">
          <img src="../../assets/imgs/userBorder1.png" width="60" height="60" alt />
          <img :src="dafaultavatar" width="60" height="60" style="border-radius:50%;" alt />
        </div>
        <div class="username">
          <div></div>
          <div class="tapUser">
            <input
              type="text"
              class="username"
              name="username"
              v-model="username"
              v-on:blur="getAvatar"
              autocomplete="off"
            />
          </div>
        </div>
        <div class="password">
          <div></div>
          <div class="tapPsd">
            <input type="password" name="password" v-model="password" />
          </div>
        </div>
        <div class="logControl control">
          <button class="loginBtn btn" @click="login"></button>
          <span class="tipText" @click="toggle"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getAvatar, Login } from "@/api/index";
export default {
  data() {
    return {
      username: "",
      password: "",
      dafaultavatar: ""
    };
  },
  methods: {
    toggle() {
      this.$parent.$parent.toggle();
      this.reset();
    },
    getAvatar() {
      if (this.username.length != 0) {
        let params = { username: this.username };
        getAvatar(params).then(res => {
          if (res.data.code === 200) {
            this.dafaultavatar = res.data.data.avatar;
          }
        });
      }
    },
    reset() {
      this.username = "";
      this.password = "";
      this.dafaultavatar = "";
    },
    login() {
      if (this.username.length != 0 && this.password.length != 0) {
        let params = { username: this.username, password: this.password };
        Login(params).then(res => {
          if (res.data.code === 200) {
            this.$notify({
              title: "",
              message: "",
              iconClass: "el-icon-check",
              duration: 2500
            });
            this.$store.commit("setcurrentUser", res.data.data);
            sessionStorage.setItem(
              "currentUser",
              JSON.stringify(res.data.data)
            );
            this.$router.push("/main");
          } else {
            this.$notify({
              title: "",
              message: res.data.msg,
              iconClass: "el-icon-close",
              duration: 2500
            });
          }
        });
      } else {
        this.$notify({
          title: "",
          message: "",
          iconClass: "el-icon-close",
          duration: 2500
        });
      }
    }
  }
};
</script>

<style scoped lang="scss">
@import "@/assets/scss/index.scss";

.tapcenter {
  width: 240px;
  margin: 10px auto;
}

.loginmodel {
  @include divabcenter;
  @include modelbase;
  @include font1_24;
  @include background($background-color-theme, 0.6);
  & .avatar {
    width: 100%;
    height: 60px;
    position: relative;
    transition: opacity 0.5s;
    & img {
      position: absolute;
      left: 50%;
      top: 0;
      &:first-child {
        z-index: 100;
        width: 80px;
        height: 80px;
        transform: translateX(-50%) translateY(-7px);
      }
      &:last-child {
        transform: translateX(-50%);
        opacity: 1;
        transition: all 0.5s;
      }
    }
  }
  & .username .tapUser,
  & .password .tapPsd {
    @extend .tapcenter;
  }
  & .control {
    position: relative;
    height: 30px;
    width: 100%;
    margin-top: 30px;
    & .btn {
      @include font1_20;
      @include background($background-color-theme);
      @include divabcenter-X(100px, 30px);
      border: 5px solid transparent;
      box-sizing: border-box;
      outline: none;
      border-image: url("../../assets/imgs/border0.png") 50 80 50 80 stretch;
    }
    & .tipText {
      font-size: 16px;
      cursor: pointer;
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
}
input {
  width: 100%;
  height: 30px;
  @include font1_20;
  border: 5px solid transparent;
  padding: 0 5px;
  border-image: url("../../assets/imgs/border0.png") 50 80 50 80 stretch;
  @include background($background-color-theme);
  outline: none;
}
</style>
