<template>
  <full-page :options="options" ref="page">
    <!--      -->
    <div class="section">
      <div class="box1">
        <div class="slide">
          <div class="bg">
            <vue-particles
              color="#fff"
              :particleOpacity="0.7"
              :particlesNumber="60"
              shapeType="circle"
              :particleSize="4"
              linesColor="#000"
              :linesWidth="1"
              :lineLinked="true"
              :lineOpacity="0.4"
              :linesDistance="150"
              :moveSpeed="2"
              :hoverEffect="true"
              hoverMode="grab"
              :clickEffect="true"
              clickMode="repulse"
              class="lizi"
            ></vue-particles>
          </div>
        </div>
        <div class="enter">
          <ghostbutton>
            <router-link to="/main">enter</router-link>
          </ghostbutton>
        </div>
        <div @click="toggle" class="next">
          <span class="iconfont iconarrowdown"></span>
        </div>
      </div>
    </div>
    <!-- -->
    <div class="section">
      <div class="box2">section2</div>
    </div>
    <!--  -->
    <div class="section">
      <div class="box3">
        <p>section3</p>

        <copyright class="copyright" />
      </div>
    </div>
  </full-page>
</template>

<script>
import copyright from "../components/copyright";
import ghostbutton from "../components/ghostbutton";
export default {
  data() {
    return {
      options: {
        licenseKey: "OPEN-SOURCE-GPLV3-LICENSE",
        afterLoad: this.afterLoad,
        scrollOverflow: false,
        navigationPosition: "left", //
        scrollBar: true,
        touchSensitivity: 5,
        keyboardScrolling: true, //true
        menu: "#menu",
        autoScrolling: false,
        sectionsColor: [
          "#FFF",
          "#eee",
          "#ddd",
          "#ccc",
          "#bbb",
          "#aaa",
          "#999",
          "#888",
          "#777"
        ]
      }
    };
  },
  components: { copyright, ghostbutton },
  methods: {
    toggle() {
      this.$refs.page.api.moveSectionDown();
    }
  }
};
</script>

<style scoped lang="scss">
.box1 {
  position: relative;
  .bg {
    width: 100%;
    height: 100%;
    background-size: contain;
    background-repeat: repeat-x;
    .lizi {
      width: 100%;
      height: 100%;
    }
  }
  .enter {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-size: 20px;
    color: aliceblue;
  }
  .next {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%); 
    animation: top_btm 0.5s ease-out infinite alternate;
    &:hover {
      animation-play-state: paused;
    }
    span {
      color: #aaa;
      font-size: 30px;
    }
  }
}
.box3 {
  height: 100%;
  position: relative;
  .copyright {
    width: 100%;
    position: absolute;
    bottom: 0;
  }
}
@keyframes top_btm {
  from{
    bottom: 30px;
  }
  to{
    bottom: 25px;
  }
}
</style>