<template>
  <div class="ghost">
    <!-- <button class="btn"></button> -->
    <div class="box">
      <slot></slot>
    </div>
    <span class="line-top line-h line"></span>
    <span class="line-right line-v line"></span>
    <span class="line-bottom line-h line"></span>
    <span class="line-left line-v line"></span>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: {
    width: {
      type: Number,
      default: 100
    },
    height: {
      type: Number,
      default: 40
    },
    duration: {
      type: Number,
      default: 0.75
    },
    color: {
      type: String,
      default: "#000"
    },
    bgcolor: {
      type: String,
      default: "#000"
    }
  },
  mounted() {
    $(".ghost").css("width", this.width, "height", this.height);
    $(".ghost").css("transition-duration", this.duration + "s");
    $(".line").css("background-color", this.color);
    $(".line").css("transition-duration", this.duration + "s");
    $(".box").css("transition-duration", this.duration + "s");
    $(".ghost").hover(function() {
      $(".box").css("background", this.bgcolor);
    });
  }
};
</script>
<style lang="scss" scoped>
.ghost {
  position: relative;
  padding: 10px;
  width: 100px;
  height: 40px;
  margin: 20px;
  .box {
    width: 100%;
    height: 100%;
    filter: blur(0.5px);
    overflow: hidden;
    background-color: transparent;
    padding: 0;
    outline: none;
  }
  .line-v {
    width: 2px;
    height: 100%;
  }
  .line-h {
    width: 100%;
    height: 2px;
  }
  .line {
    position: absolute;
    opacity: 0;
    display: block;
  }
  .line-top {
    top: 0;
    left: 0;
    transform: translateX(-150%);
  }
  .line-right {
    top: 0;
    right: 0;
    transform: translateY(-150%);
  }
  .line-bottom {
    bottom: 0;
    left: 0;
    transform: translateX(150%);
  }
  .line-left {
    top: 0;
    left: 0;
    transform: translateY(150%);
  }
  &:hover {
    .line-top {
      opacity: 1;
      transform: translateX(0);
    }
    .line-right {
      opacity: 1;
      transform: translateY(0);
    }
    .line-bottom {
      opacity: 1;
      transform: translateX(0);
    }
    .line-left {
      opacity: 1;
      transform: translateY(0);
    }
  }
}
</style>